body{
    display: flex;
    justify-content: center;
    min-height: 100vh;

    color: white;
    background: linear-gradient(var(--clr-bgr-300), rgb(29, 43, 47), rgb(15, 20, 21) 100vh);
    transition: box-shadow 1s ease;
    .glow{
        position: absolute;
        top: 1rem;
        width: calc(100% - 2rem);
        max-width: 1000px;
        aspect-ratio: 3/2;
        z-index: -1;
        border-radius: 1rem;
        filter: brightness(150%);

        transition: box-shadow 1s ease;
    }
    &.menu-opened .glow{
        box-shadow: 0 0 14rem 1rem var(--clr-acc-100);
    }
}

#root{
    width: 100%;
    flex-grow: 1;
    max-width: 1000px;

    display: flex;
    flex-direction: column;
}

.shadow{
    box-shadow: .3rem .7rem 4rem 4px rgba(0, 0, 0, 0.4);
}

.gap{
    height: 5px;
    background: linear-gradient(-90deg, var(--clr-acc-100), var(--clr-acc-200), var(--clr-acc-100));
    filter: brightness(120%);
    margin: 5px;
    border-radius: 2rem;
}

@media (min-width: 400px) {
    #root{
        margin: 1rem;
        border-radius: 3rem;
    }

    .collection-section{border-radius: .5rem .5rem 1rem 1rem;}

    .menu-section{
        border-radius: 1rem 1rem .5rem .5rem;
        .logo .description {font-size: 1.1rem !important;}
    }
}